export default {
    name: 'Index',

    data() {
        return {
            allData: [],
            error: null,
            urlApi: 'http://194.87.111.244/api/',
        }
    },

    async fetch() {
        if (process.client) {
            document.body.scrollTop = document.documentElement.scrollTop = 0
        }
        try {
            this.allData = await this.$strapi.find('categories/index')
        } catch (error) {
            this.error = error
        }
    },
}
